
import {mapMutations,mapState} from 'vuex'
import request from 'common/requesst/interface.js'
import {getImgUrl} from 'common/fileUtil.js'
export default {
	computed:{
		...mapState(['token'])
	},
	method: {
		...mapMutations(['setPersonInfo'])
	},
	onLaunch: function() {
		console.log('App Launch');
		if (this.token) {
			request({
				url: '/user',
				method: 'GET',
				handle: true
			}).then( res => {
				if (res.data.code === 1000) {
					res.data.data.personalImage = getImgUrl(res.data.data.personalImage)
					this.$store.commit('setPersonInfo',res.data.data)
				}
			})
		}










	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
